import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import * as moment from 'moment';

import { VideoView } from './video-view';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class VideosViewsService extends ListItemsService<VideoView>  {


  getListTitle(): string {
    return 'Просмотры видео';
  }

  getListRelativePath(): string {
    return '/Lists/slVideosViews';
  }

  getSelect(): string {
    return 'Id,Title,slVideoLookup/Id,slVideoLookup/Title,LikedBy/Id,LikesCount';
  }

  getExpand(): string {
    return 'slVideoLookup,LikedBy';
  }

  getOrderBy(): [string, boolean][] {
    return null;
  }

  getTop(): number {
    return 5000;
  }

  convertListItemToEntity(item: any): VideoView {
    const view: VideoView = {
      id: item.Id,
      title: item.Title,
      videoLookupId: null,
      count: item.LikesCount,
      userIds: item.LikedBy && item.LikedBy.map(l => l.Id) || []
    };
    if (item.slVideoLookup) {
      view.videoLookupId = item.slVideoLookup.Id;
    }
    return view;
  }

  convertEntityToListItem(entity: VideoView) {
    return {
      Id: entity.id,
      Title: entity.title,
      slVideoLookupId: entity.videoLookupId
    };
  }

  getMockItems(): VideoView[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

  getItemsByNewsId(ids: number[]): Observable<VideoView[]> {
    if (ids && ids.length > 0) {
      let filter = '';
      for (const id of ids) {
        filter += `slVideoLookup/Id eq ${id} or`;
      }
      filter = filter.slice(0, -3);
      return this.getItems({ filter: filter, top: ids.length });
    } else {
      return of([]);
    }
  }
}
