import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { map, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { fromEvent } from 'rxjs';

import { Video } from 'src/app/services/videos/video';
import { VideosService } from 'src/app/services/videos/videos.service';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { VideosViewsService } from 'src/app/services/videos-views/videos-views.service';
import { VideoView } from 'src/app/services/videos-views/video-view';

@Component({
  selector: 'app-videogallery-page',
  templateUrl: './videogallery-page.component.html',
  styleUrls: ['./videogallery-page.component.scss']
})
export class VideogalleryPageComponent implements OnInit, AfterViewInit {

  @ViewChild('searchRef') searchRef: ElementRef;
  items: Video[] = [];
  search: string = null;

  constructor(
    private sidebarService: SidebarService,
    private videosService: VideosService,
    private route: ActivatedRoute,
    private router: Router,
    private videosViewsService: VideosViewsService
  ) {
    this.sidebarService.handleSetSettings({ showSidebar: false, bgColor: '#fff' });
  }

  ngOnInit() {
    this.route.params
      .pipe(map(data => data.search))
      .subscribe((search: string) => {
        this.search = search;
      });
    this.route.data
      .pipe(map(data => data.videos))
      .subscribe((items: Video[]) => {
        this.items = items;
        this.loadViews(items);
      });
  }

  ngAfterViewInit(): void {
    fromEvent(this.searchRef.nativeElement, 'keyup')
      .pipe(
        map((e: any) => e.target.value),
        debounceTime(1000),
        distinctUntilChanged())
      .subscribe((query: string) => this.router.navigate(['/videogallery-search', query]));
  }

  getEnding(number: number, endings: string[]) {
    let ending: string;
    if (number) {
      number = number % 100;
      if (number >= 11 && number <= 19) {
        ending = endings[2];
      } else {
        const i = number % 10;
        switch (i) {
          case (1): ending = endings[0]; break;
          case (2):
          case (3):
          case (4): ending = endings[1]; break;
          default: ending = endings[2];
        }
      }
      ending = `${number} ${ending}`;
    } else {
      ending = `Нет ${endings[2]}`;
    }
    return ending;
  }

  private loadViews(videos: Video[]) {
    let filter = '';
    if (videos.length > 0) {
      for (const n of videos) {
        filter += `slVideoLookup/Id eq ${n.id} or `;
      }
      filter = filter.slice(0, -4);
      this.videosViewsService.getItems({ filter: filter, top: 5000 })
        .subscribe((views: VideoView[]) => {
          for (const n of this.items) {
            const view = views.filter(c => c.videoLookupId === n.id);
            if (view && view.length > 0) {
              n.viewsCount = view[0].count;
            } else {
              n.viewsCount = 0;
            }
          }
        });
    }
  }
}
