import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { VideogalleryPageComponent } from './components/videogallery-page/videogallery-page.component';
import { VideoalbumPageComponent } from './components/videoalbum-page/videoalbum-page.component';

import { VideosResolver } from 'src/app/services/videos/videos.resolver';
import { NextVideoResolver } from 'src/app/services/videos/next-video.resolver';
import { VideoResolver } from 'src/app/services/videos/video.resolver';

const routes: Routes = [
  {
    path: 'search',
    component: VideogalleryPageComponent,
    resolve: { videos: VideosResolver},
    data: { animation: 'fadeAnimation', title: 'Видеогалерея' }
  },
  {
    path: 'search/:search',
    component: VideogalleryPageComponent,
    resolve: { videos: VideosResolver},
    data: { animation: 'fadeAnimation', title: 'Видеогалерея' }
  },
  {
    path: 'item/:id',
    component: VideoalbumPageComponent,
    resolve: { video: VideoResolver, next: NextVideoResolver },
    data: { animation: 'fadeAnimation', title: 'Видео' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [VideoResolver, NextVideoResolver, VideosResolver],
    exports: [RouterModule]
})
export class VideogalleryRoutingModule { }
