import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { Video } from './video';
import { VideosService } from './videos.service';

@Injectable()
export class VideosResolver implements Resolve<Video[]> {

  constructor(private videosService: VideosService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Video[]> {
    const search = route.params.search;
    const filter = search == null || search === '' ? undefined : `substringof('${search}',Title)`;
    return this.videosService.getItems({filter: filter});
  }
}
