import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute } from '@angular/router';

import { Observable, of } from 'rxjs';
import { Video } from './video';
import { VideosService } from './videos.service';
import { flatMap } from 'rxjs/operators';

@Injectable()
export class NextVideoResolver implements Resolve<Video> {

  constructor(private videosService: VideosService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Video> {
    const id = route.params.id;
    return this.videosService.getItems({ filter: `Id gt ${id}`, orderBy: [['Id', true]], top: 1 })
      .pipe(flatMap((videos: Video[]) => {
        return videos && videos.length > 0 ? of(videos[0]) : of(null);
      }));
  }
}
