import {
  Component,
  OnInit,
  AfterViewInit,
  ViewChild,
  ElementRef
} from '@angular/core';
import { Subscription, fromEvent } from 'rxjs';
import {
  debounceTime,
  map,
  distinctUntilChanged
} from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';

import { Video } from 'src/app/services/videos/video';
import { SidebarService } from 'src/app/services/sidebar/sidebar.service';
import { VideosViewsService } from 'src/app/services/videos-views/videos-views.service';
import { PortalService } from 'src/app/services/portal/portal.service';
import { VideoView } from 'src/app/services/videos-views/video-view';

@Component({
  selector: 'app-videoalbum-page',
  templateUrl: './videoalbum-page.component.html',
  styleUrls: ['./videoalbum-page.component.scss']
})
export class VideoalbumPageComponent implements OnInit, AfterViewInit {
  @ViewChild('searchRef') searchRef: ElementRef;
  videoSubscription: Subscription;
  public item: Video = null;
  public next: Video = null;

  constructor(
    private sidebarService: SidebarService,
    private route: ActivatedRoute,
    private router: Router,
    private videosViewsService: VideosViewsService,
    private sharepointService: PortalService
  ) {
    this.sidebarService.handleSetSettings({
      showSidebar: false,
      bgColor: '#fff'
    });
  }

  ngOnInit() {
    this.route.data.pipe(map(data => data.video)).subscribe(video => {
      this.item = video;
      this.view(this.item, true);
    });
    this.route.data.pipe(map(data => data.next)).subscribe(next => {
      this.next = next;
      if (this.next) {
        this.view(this.next);
      }
    });
  }

  view(video: Video, isCreate?: boolean) {
    const userId = this.sharepointService.getCurrentUserContext().userId;
    this.videosViewsService
      .getItems({ filter: `slVideoLookup/Id eq ${video.id}` })
      .subscribe((views: VideoView[]) => {
        if (views && views.length > 0) {
          video.viewsCount = views[0].count;
          if (views[0].userIds.every(id => id !== userId) && isCreate) {
            this.videosViewsService.addLike({id: views[0].id});
          }
        } else {
          if (isCreate) {
            this.videosViewsService
              .createItem({
                id: undefined,
                title: video.title,
                videoLookupId: video.id
              })
              .subscribe(view => this.videosViewsService.addLike({id: view.id}));
          }
        }
      });
  }

  ngAfterViewInit(): void {
    fromEvent(this.searchRef.nativeElement, 'keyup')
      .pipe(
        map((e: any) => e.target.value),
        debounceTime(1000),
        distinctUntilChanged()
      )
      .subscribe((query: string) =>
        this.router.navigate(['/videogallery-search', query])
      );
  }

  getEnding(number: number, endings: string[]) {
    let ending: string;
    if (number) {
      number = number % 100;
      if (number >= 11 && number <= 19) {
        ending = endings[2];
      } else {
        const i = number % 10;
        switch (i) {
          case 1:
            ending = endings[0];
            break;
          case 2:
          case 3:
          case 4:
            ending = endings[1];
            break;
          default:
            ending = endings[2];
        }
      }
      ending = `${number} ${ending}`;
    } else {
      ending = `Нет ${endings[2]}`;
    }
    return ending;
  }
}
